<template>
  <div class="billing row">
    <div class="col-12 mt-3" v-if="isReady">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              circle
              type="primary"
              class="no-width mr-2 flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">Billing history</h5>
          </div>
        </div>

        <div class="col-12 list-table">
          <div class="box pt-3">
            <div class="row">
              <div class="col-12 flex-align-center">
                <p class="mx-0 table-tab active-table-tab">All</p>
              </div>
              <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div>
              <div
                class="
                  col-12
                  mb-1
                  d-flex
                  justify-content-between
                  align-items-center
                "
              >
                <div class="flex-align-center">
                  <el-select
                    v-model="filter.pageSize"
                    class="mr-2 page-size-selector mr-2"
                  >
                    <el-option
                      v-for="page in 10"
                      :key="page"
                      :label="page * 5"
                      :value="page * 5"
                    >
                    </el-option>
                  </el-select>
                  <el-select
                    v-model="filter.status"
                    filterable
                    clearable
                    class="mr-2 filter-input"
                    placeholder="Status"
                  >
                    <el-option label="Active" value="Active"> </el-option>
                    <el-option label="Canceled" value="Canceled"> </el-option>
                    <el-option label="Pending" value="Pending"> </el-option>
                  </el-select>
                  <el-date-picker
                    class="mr-2 filter-input"
                    v-model="filter.date"
                    type="date"
                    format="yyyy-MM-dd"
                    value-format="yyyy-MM-dd"
                    placeholder="Pick a date"
                  >
                  </el-date-picker>
                </div>
                <el-popover placement="bottom" width="200" trigger="click">
                  <p class="font-weight-bold">Sort By</p>
                  <div class="b-divider my-2"></div>
                  <div class="w-100">
                    <el-radio
                      v-model="filter.order_by"
                      v-for="(sort, n) in sortList"
                      :key="sort.name"
                      :class="n !== sortList.length - 1 && 'mb-3'"
                      :label="sort.value"
                    >
                      {{ sort.name }}
                    </el-radio>
                  </div>
                  <el-button
                    class="mr-0 flat no-width"
                    v-ripple.400
                    slot="reference"
                    type="dark"
                  >
                    <i class="bx bx-sort mr-1"></i> Sort
                  </el-button>
                </el-popover>
              </div>
            </div>

            <div class="w-100 el-main-table" v-loading="loading">
              <el-table
                ref="multipleTable"
                :data="billings.results"
                style="width: 100%"
                @row-click="
                  (row) =>
                    $router.push({
                      name: 'Billing',
                      query: { bid: row.id },
                    })
                "
              >
                <el-table-column label="Status">
                  <template slot-scope="scope">
                    <el-tag
                      type="success"
                      class="rounded"
                      v-if="scope.row.status == 'Active'"
                      >{{ scope.row.status }}</el-tag
                    >

                    <el-tag
                      type="danger"
                      class="rounded"
                      v-if="scope.row.status == 'Canceled'"
                      >{{ scope.row.status }}</el-tag
                    >

                    <el-tag
                      type="warning"
                      class="rounded"
                      v-if="scope.row.status == 'Pending'"
                      >{{ scope.row.status }}</el-tag
                    >
                  </template>
                </el-table-column>

                <el-table-column label="Payment">
                  <template slot-scope="scope">
                    <el-tag
                      type="success"
                      class="rounded"
                      v-if="scope.row.payment_status == 'Paid'"
                      >{{ scope.row.payment_status }}</el-tag
                    >

                    <el-tag
                      type="danger"
                      class="rounded"
                      v-if="scope.row.payment_status == 'Unpaid'"
                      >{{ scope.row.payment_status }}</el-tag
                    >

                    <el-tag
                      type="warning"
                      class="rounded"
                      v-if="scope.row.payment_status == 'Partially Paid'"
                      >{{ scope.row.payment_status }}</el-tag
                    >
                  </template>
                </el-table-column>

                <el-table-column property="date" label="Date">
                </el-table-column>
                <el-table-column label="Plan">
                  <template slot-scope="scope">
                    {{ scope.row.selected_plan.name }}
                  </template>
                </el-table-column>
                <el-table-column property="supplier_name" label="No. of months">
                  <template slot-scope="scope">
                    {{ scope.row.number_of_months }}x{{ scope.row.price }}
                  </template>
                </el-table-column>
                <el-table-column label="Total">
                  <template slot-scope="scope">
                    {{ scope.row.total }}MMK
                  </template>
                </el-table-column>
                <template #empty>
                  <el-empty
                    description="No data foun!"
                    :image-size="200"
                  ></el-empty>
                </template>
              </el-table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import { url, appUrl } from "../../../../core/constant/data";
export default {
  data() {
    return {
      url: url,
      appUrl: appUrl,
      loading: false,
      billings: {
        current_page: 1,
        results: [],
      },
      filter: {
        order_by: "-updated_at",
        pageSize: 10,
        date: "",
        status: "",
      },
      sortList: [
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
      ],
    };
  },
  watch: {
    filter: {
      handler: function () {
        this.getBillings();
      },
      deep: true,
    },
  },
  created() {
    this.$store.commit("setIsReady", false);
    this.getBillings();
  },
  mounted() {},
  computed: {
    ...mapState(["store", "isReady"]),
    ...mapGetters(["checkIsReady"]),
  },
  methods: {
    getBillings() {
      this.loading = true;
      if (this.filter.date === null) {
        this.filter.date = "";
      }
      var url = `billings/?page=${this.billings.current_page}&page_size=${this.filter.pageSize}&status=${this.filter.status}&date=${this.filter.date}&order_by=${this.filter.order_by}`;
      this.$axios
        .get(url)
        .then((response) => {
          this.billings = response.data;
          this.loading = false;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.$noti.display({ message: err, status: "error", color: "error" });
        });
    },
  },
};
</script>
